<template>
	<!-- 导航栏 -->
	<view >
		<view class="bg-light" :class="fixed?'fixed-top':''">
			<!-- 状态栏 -->
			<view :style="'height:'+ statusBarHeight+'px'"></view>
			<!-- 导航栏 -->
			<view class="w-100 flex align-center justify-between" style="height: 90rpx;">
				<!-- 左边 -->
				<view class="flex align-center">
					<!-- 标题 -->
					<text v-if="title"  class="font-md ml-3"
					>微信<slot name="title"></slot></text>
				</view>
				<!-- 右边 -->
				<view class="flex align-center">
					<free-icon-button :icon="'\ue6e3'" @click="search"></free-icon-button>
					<free-icon-button :icon="'\ue682'" @click="openExtend"></free-icon-button>
				</view>
			</view>
		</view>
		<!-- 占位 -->
		<view v-if="fixed" :style="fixedStyle"></view>
	</view>
</template>

<script>
	import freeIconButton from '@/components/free-ui/free-icon-button.vue'
	export default {
		props:{ 
			title:{
				type:Boolean,
				default:false
			},
			fixed:{
				type:Boolean,
				default:true
			}
		},
		components:{
			freeIconButton
		},
		data(){
			return {
				statusBarHeight:0,
				navBarHeight:0
			}
		},
		mounted() {
			// #ifdef APP-PLUS-NVUE
			this.statusBarHeight = plus.navigator.getStatusbarHeight()
			// #endif
			this.navBarHeight = this.statusBarHeight + uni.upx2px(90)
		},
		computed:{
			fixedStyle(){
				return `height:${this.navBarHeight}px`
			}
		},
		methods:{
			openExtend(){
				this.$emit('openExtend')
			}
		}
	}
</script>

<style>
</style>
